import React from 'react';
import { useParams } from 'react-router-dom';
import dataNews from '../../data/dataNews'; // Importm baza de date cu anunuri
import { SlideshowLightbox } from 'lightbox.js-react';
import 'lightbox.js-react/dist/index.css';
import Title from '../Utils/Title';
function NewsDetails() {
   // Folosim useParams pentru a obine parametrul 'id' din URL
   const { id } = useParams();

   // Gsim anunul corespunztor din baza de date utiliznd 'id'
   const anunt = dataNews.find((anunt) => anunt.id === parseInt(id));
   console.log(anunt);
   if (!anunt) {
      return <div>Anunul nu a fost gsit.</div>;
   }

   return (
      <section className="news-details">
         <div className="container">
            <div className="news-details__body">
               <Title class="news-details__title">{anunt.title}</Title>
               <p className="news-details__text">{anunt.description}</p>
               <SlideshowLightbox className="news-details__container ">
                  {anunt.imgs.map((img, index) => (
                     <img
                        className="news-details__img"
                        key={index}
                        src={anunt.imgs[index]}
                        alt={`Imagine ${index + 1}`}
                        loading="lazy"
                     />
                  ))}
               </SlideshowLightbox>
            </div>
         </div>
      </section>
   );
}

export default NewsDetails;
