function ScurtIstoric() {
   const smiles = ['()', '()', '(o)', '()', '(._.`)', '(_)'];
   const randNum = Math.floor(Math.random() * (6 - 0) + 0);
   const smile = smiles[randNum];
   console.log(randNum);
   return (
      <main className="main">
         <section className="error">
            <p className="error__title">{smile}</p>
            <p className="error__text">
               Unfortunately, this page doesn't exist.
            </p>
         </section>
      </main>
   );
}
export default ScurtIstoric;
